import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, RouterModule } from '@angular/router';
import { User } from '@biz-nest/auth-client';
import {
  FrontendLogger,
  getBizNestErrorResponseMessage
} from '@biz-nest/core-client';
import {
  AppNameService, MainBarTitleService,
  scrollToTop,
  WindowService
} from '@front-shared-lib';
import { EagleInfoClientError } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { ManageUserProfileCoreComponent } from '../manage-user-profile-core/manage-user-profile-core.component';

@Component({
  selector: 'app-manage-user-profile',
  standalone: true,
  imports: [
    RouterModule,
    AdminPageContentComponent,
    ManageUserProfileCoreComponent
  ],
  templateUrl: './manage-user-profile.component.html',
  styleUrl: './manage-user-profile.component.scss'
})
export class ManageUserProfileComponent
implements OnInit {
  user?: User;
  id?: string;
  loading = false;


  constructor(
    private windowService: WindowService,
    private route: ActivatedRoute,
    private apNme: AppNameService,
    private toastrService: ToastrService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    this.id = this.route.snapshot?.params['id'];
  }

  ngOnInit(): void {
    this.setTitle();
    scrollToTop(this.windowService);
    if (!this.user) {
      this.getUser();
    }
  }

  setTitle() {
    if (this.user) {
      this.mainBarTitle.title = 'Manage User Profile ' + this.user.firstName + ' ' + this.user?.lastName;
      this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
      this.metaTagService.updateTag({
        name: 'description',
        content: `Easily manage and view user profiles for your
      organization to ensure effective oversight and engagement.`
      });
    }
  }

  async getUser() {
    if (!this.id) {
      throw new EagleInfoClientError('no user id');
    }

    try {
      this.loading = true;
      const user = await User.getOne(this.id);

      if (user) {
        this.user = user;
        this.setTitle();
      }
    } catch (error) {
      FrontendLogger.error(error);
      this.toastrService.error(getBizNestErrorResponseMessage(error), 'Error');
    } finally {
      this.loading = false;
    }
  }
}
